import React from 'react';
import PropTypes from 'prop-types';

class Forms extends React.PureComponent {  
  render() {
    return (
      <form
        id={this.props.id}
        className={this.props.class}
        noValidate={this.props.noValidate}
        autoComplete={this.props.autoComplete}
      >
        {this.props.children}
      </form>
    );
  };
}

Forms.propTypes = {
  children: PropTypes.node.isRequired,
  
  // The id of the input element. Use that property to make label and helperText accessible for screen readers. //
  id: PropTypes.string,

  // Type attribute of the Class element. //
  className: PropTypes.string,

  // Indicate that the form is not to be validated on submit //
  noValidate: PropTypes.bool,

  // An HTML form with autocomplete on (and off for one input field) //
  autoComplete: PropTypes.oneOf(['on', 'off']),
};

export default Forms;
